    .titulo-segundo
      #t_1_1.h4 1.1 Conceptos bsicos para promover la donacin de sangre   

    p.mb-5 Por lo general se ha concebido que, para ejercer la labor de promotor, se requiere nicamente de los conceptos relacionados con atencin a los donantes y liderazgo. Aunque estos temas son fundamentales para el ejercicio de sus actividades, es importante tener en cuenta que, dada la responsabilidad que tienen estos actores de la red de sangre, <b> tambin es clave el captar donantes de bajo riesgo, mantener la disponibilidad necesaria para responder a las necesidades transfusionales,</b> emitir mensajes que conlleven a que la poblacin adopte hbitos de vida saludables y tomar decisiones para que el sistema transfusional sea eficaz y eficiente. As, se requiere de conocimientos tcnicos que le permita al promotor cumplir con el objetivo de su labor, es por esto que, en este aparte del curso, se revisarn algunos conceptos que el promotor de sangre debe conocer, pero sobre todo, implementar.

