    .h5 Qu es la sangre?

    p.mb-5 La sangre es un tejido lquido que aporta nutrientes a los rganos y tejidos del cuerpo. Es transportada a travs de los vasos sanguneos (venas, arterias y capilares) por todo el cuerpo. Est formada por un componente lquido conocido como plasma, que equivale al 55%, y un componente celular equivalente al 45%.

    .row.justify-content-center.align-items-center.mb-5
      .col-auto      
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_1.png", alt="alt", style="width:650px")

    .row.justify-content-center.align-items-center.mb-4
      .col-auto  
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/ico_19.svg", alt="alt", style="width:120px")
      .col-lg
        p #[strong Glbulos rojos] #[br] Entre las clulas que conforman la sangre, se tienen a los glbulos rojos, tambin denominados eritrocitos, cuya funcin principal es transportar oxgeno.    

    p.mb-4 De acuerdo a la organizacin panamericana de la salud (2005), la hemoglobina le proporciona a la sangre su color rojo, es la sustancia cuya presencia resulta necesaria para que el oxgeno sea obtenido de los pulmones. Los glbulos rojos tambin transportan el oxgeno utilizado, transformado en dixido de carbono, de regreso a los pulmones para su expulsin del cuerpo. El hierro es un elemento clave para la elaboracin de la hemoglobina. Cuando las reservas de hierro son insuficientes, el individuo se convierte en anmico, con la consecuente prdida de la capacidad de transportar el oxgeno. (p.16)    

