        .col-sm
          p De acuerdo con lo anterior, <b> es importante que las personas incluyan en su dieta alimentos ricos en hierro </b> como, por ejemplo, hgado, frijoles, lentejas, espinacas, acelgas, entre otros.

    .row.justify-content-center.align-items-center
