    .h5 Componentes sanguneos (hemocomponentes)
    
    p.mb-5 Al donar sangre se puede obtener sangre total o hemocomponentes.

    TabsA.mb-5.mb-5
      .tarjeta.tarjeta--amarilla--borde.p-4(titulo="Sangre total")
        .h4 Sangre total
        p.mb-4 Es obtenida a partir de un donante. La bolsa en la cual se colecta contiene un lquido llamado anticoagulante, el cual evita que la sangre se coagule despus de haber sido extrada del torrente sanguneo. 

        p.mb-4 "Su principal uso es como producto inicial para la preparacin de otros componentes sanguneos", esto con el fin de lograr un mayor aprovechamiento, es decir que se le transfunde al paciente solamente el hemocomponente que requiere, as mismo, busca de evitar reacciones adversas.(Instituto Nacional de Salud-Coordinacin Red Nacional de Bancos de Sangre. 2011)

        p Las unidades de sangre total se fracciona mediante un proceso denominado centrifugacin, (mtodo por el cual se pueden separar slidos de lquidos de diferente densidad por medio de una fuerza giratoria), es decir, que este proceso permite que cada clula se pueda obtener de manera separada y as es como se obtienen los diferentes hemocomponentes: 
        
      
      .tarjeta.tarjeta--amarilla--borde.p-4(titulo="Componentes sanguneos")
        .h4 Componentes sanguneos o hemocomponentes
        p.mb-4 Son las clulas sanguneas como glbulos rojos, plaquetas; los fluidos corporales como plasma y sus fracciones como crioprecipitados, que pueden prepararse por mtodos como centrifugacin, sedimentacin, entre otros #[i (Instituto Nacional de Salud-Coordinacin Red Nacional de Bancos de Sangre 2011)].
        p Es necesario aclarar que en los bancos de sangre se obtienen hemocomponentes y no hemoderivados, ya que estos ltimos son los factores de coagulacin que son obtenidos mediante el procesamiento industrial del plasma, acciones que a la fecha no se realizan en los bancos de sangre del pas.
      .tarjeta.tarjeta--amarilla--borde.p-4(titulo="Glbulos rojos estndar")
        .h4 Glbulos rojos estndar 
        p "Es el componente sanguneo obtenido al separar la mayor parte del plasma de la sangre total, por centrifugacin o sedimentacin en cualquier momento antes de la fecha de caducidad. Por general, es utilizado cuando hay hemorragias que conllevan al paciente a una prdida considerable de sangre, tambin se usa en pacientes diagnosticados con anemia, cncer, leucemia, entre otros".#[i (Instituto Nacional de Salud-Coordinacin)]
      
      .tarjeta.tarjeta--amarilla--borde.p-4(titulo="Concentrado de plaquetas unitarior")
        .h4 Concentrado de plaquetas unitario
        p "Componente sanguneo obtenido de una unidad de sangre total que contiene la mayor parte de las plaquetas de esta, suspendidas en plasma u otras soluciones aditivas. Por lo general, es utilizado en pacientes con cncer, leucemia, entre otros." #[i (Instituto Nacional de Salud-Coordinacin Red Nacional de Bancos de Sangre 2011)].
      
      .tarjeta.tarjeta--amarilla--borde.p-4(titulo="Plasma fresco congelado (PFC)")
        .h4 Plasma fresco congelado (PFC): 
        p Es la parte lquida de la sangre, puede obtenerse por dos vas: la primera de sangre total mediante el proceso de centrifugacin o tambin puede obtenerse en el momento de la extraccin afresis a travs de equipos automatizados. Contiene factores lbiles de la coagulacin, protenas plasmticas; puede ser usado en pacientes quemados, entre otros #[i (Grupo Cooperativo Iberoamericano de Medicina Transfusional GCIAMT 2012; Salazar 2003)]
      
      .tarjeta.tarjeta--amarilla--borde.p-4(titulo="Crioprecipitado")
        .h4 Crioprecipitado
        p "Es la fraccin que se precipita al descongelar y centrifugar el PFC a 4 C, se elimina el sobrenadante y se conserva el precipitado en un pequeo volumen de plasma que contiene fibringeno y factores de coagulacin." #[i (Sociedad Espaola de Transfusin Sangunea y Terapia Celular SETS 2015).]

    p.mb-4 #[strong Cadena de frio:] es un proceso que se realiza sistemticamente, para conservar y transportar  los hemocomponentes desde que es extrado al donante, hasta su administracin al paciente (Organizacin Mundial de la Salud 2004).  
      
    p.mb-5 Cada hemocomponente tiene caractersticas de tiempo y temperatura especficas para lo cual se hace necesario mantener la cadena de frio.

