    .h5.mb-4 Grupo sanguneo y factor Rh

    .cajon.cajon--gris.mb-4
      .row.justify-content-center.align-items-center
        .col-auto
          figure
            img(src="@/assets/curso/images/pages/ilustraciones/ico_15.svg", alt="alt", style="width:120px;")          
        .col-sm
          p En 1901 Karl Landsteiner public un artculo en el cual referenciaba la existencia de tres (3) grupos sanguneos: A, B y O, en los glbulos rojos en el individuo; un ao despus, #[strong  Decastello y Sturli, descubrieron el grupo AB.] Es de anotar que el grupo sanguneo se expresa debido a unas molculas denominadas antgenos, que se encuentran en la superficie de la membrana de los glbulos rojos. #[i (Beltrn, Ayala, and Jara 1999)].    

    p.mb-5 En el sistema ABO se han descrito cuatro (4) grupos <b> A, B, AB, O; y los antgenos, que son diferentes en cada grupo sanguneo.</b>

    .row.justify-content-center.align-items-center
      .col-12.col-lg-4
        .v_avatar__crd
          .v_avatar__crd__content
            figure.mb-3
              img(src="@/assets/curso/images/pages/ilustraciones/ico_10.svg", alt="alt")
            h6.text-center Grupo sanguneo A
            p.text-center Expresa antgenos A en su superficie y presentan anticuerpos anti- B en el plasma.

      .col-12.col-lg-4
        .v_avatar__crd
          .v_avatar__crd__content
            figure.mb-3
              img(src="@/assets/curso/images/pages/ilustraciones/ico_13.svg", alt="alt")
            h6.text-center Grupo sanguneo B
            p.text-center Expresa antgenos B en su superficie, y en su plasma presentan anticuerpos anti- A.

    .row.justify-content-center.align-items-center.mb-5
      .col-12.col-lg-4
        .v_avatar__crd
          .v_avatar__crd__content
            figure.mb-3
              img(src="@/assets/curso/images/pages/ilustraciones/ico_11.svg", alt="alt")
            h6.text-center Grupo sanguneo AB
            p.text-center Expresa antgenos A y B en su superficie y no tiene anticuerpos contra estos, por esto ltimo el individuo de este grupo sanguneo se considera receptor universal para glbulos rojos.

      .col-12.col-lg-4
        .v_avatar__crd
          .v_avatar__crd__content
            figure.mb-3
              img(src="@/assets/curso/images/pages/ilustraciones/ico_12.svg", alt="alt")
            h6.text-center Grupo sanguneo O
            p.text-center No expresa antgenos en su superficie, pero en su plasma presentan anticuerpos anti-A y anti-B. El individuo de este grupo sanguneo se considera como el donante universal para glbulos rojos.  


    .row.justify-content-center.align-items-center.mb-5
      .col-auto     
        h5.text-center.mb-4 Grupos sanguneos   
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_4.png", alt="alt", style="width:1000px; display:block; margin:0 auto;")    

    .cajon.cajon--rojo.p-4.mb-5    
      p El sistema Rh es el segundo ms importante (el primero es el ABO), y su clasificacin se da por la presencia del antgeno D (Rh positivos) y ausencia de este (Rh negativo).
      br
      p Los dos sistemas principales para determinar el grupo sanguneo de una persona son el sistema ABO y el sistema Rh, y son los responsables de que un donante y un receptor sean compatibles en una transfusin de sangre.

    .row.mb-5     
      .col-12.col-lg-5
        .bloque-texto-c.p-4.mb-4.mb-lg-0     
          .h5.mb-2 El grupo sanguneo O negativo le puede donar su sangre a cualquier grupo sanguneo, mientras que el grupo sanguneo AB positivo, puede recibir sangre de cualquier grupo sanguneo
            
      .col-12.col-lg
        p Para disminuir riesgos de reacciones adversas en la administracin de componentes sanguneos, <b> la normatividad vigente obliga a la realizacin de pruebas de compatibilidad </b> entre el paciente que va a recibir la transfusin y la unidad a administrar.
        br
        p Debido a que los donantes de sangre se interesan por conocer a qu pacientes pueden beneficiar con su sangre, segn el grupo sanguneo, es importante que el promotor maneje esta informacin. Como, por ejemplo, que el grupo sanguneo O negativo le puede donar su sangre a cualquier grupo sanguneo, mientras que el grupo sanguneo AB positivo, puede recibir sangre de cualquier grupo sanguneo, tal como se muestra en la siguiente figura.

    .row.justify-content-center.align-items-center.mb-5
      .col-auto
        h5.text-center.mb-4 Esquema donacin/recepcin 
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_5.svg", alt="alt", style="width:700px; display:block; margin:0 auto;")

    p <b> La mayora de los colombianos, son grupo sanguneo O positivo, </b> por esta razn es que este es muy requerido, ya que, al haber ms poblacin con dicho grupo, hay ms pacientes con el mismo. <b> Le siguen en orden de necesidad el A y el B positivos.</b>
    br
    p.mb-5 Cuando se requieren donantes con grupo sanguneos Rh negativo, su necesidad se hace ms evidente, ya que la poblacin que posee este grupo sanguneo es mnima, de ah la importancia de sensibilizarlos para que hagan parte de los clubes de donantes de sangre de los bancos, ya que el tener su disponibilidad y datos de contacto a la mano, <b> permiten una mayor oportunidad al momento de ser requeridos. </b>

    .h5.mb-4 Agentes infecciosos de inters en banco de sangre

    p La sangre salva diariamente miles de vidas a travs de la transfusin de componentes sanguneos, y como parte del proceso de seguridad transfusional, a esta se le realiza tambin pruebas para deteccin de agentes infecciosos que son transmisibles por va sangunea, la normatividad vigente establece las pruebas mnimas obligatorias que deben realizarse a todas las unidades de sangre obtenidas en Colombia.
    br
    p.mb-5 As, el Decreto 1571 de 1993 establece lo siguiente: 

    .row.justify-content-center.align-items-center.mb-4
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/ico_43.svg", alt="alt", style="width:400px;")           

    p.mb-4 Los bancos de sangre, cualquiera que sea su categora, <b> deben obligatoriamente practicar bajo su responsabilidad a todas y cada una de las unidades recolectadas las siguientes pruebas: </b>

    .row.justify-content-center.mb-4
      .col-12
        ol.lista-ol--cuadro.lista-ol--separador
