          li 
            .lista-ol--cuadro__vineta
              span 5
            | Otros que de acuerdo con los estudios de vigilancia epidemiolgica se establezcan para una regin determinada por parte del Ministerio de Salud (Art. 42)

    p.mb-5 As mismo, #[strong la Resolucin 1738 de 1995 obliga a la realizacin de prueba para deteccin de #[i Trypanosoma cruzi] ] y la resolucin 0437 de 2014, se refiere la obligatoriedad de realizar la prueba de anticuerpos contra el virus Linfotrpico de clulas T Humanas (HTLV I y II) y anticuerpos totales contra el antgeno core del virus de la Hepatitis B, tal como se expone en la siguiente tabla.

