    p.mb-4 Es decir,<b> en Colombia a toda la sangre donada se le realizan siete marcadores infecciosos: VIH, Hepatitis B, Hepatitis C, Chagas, Sfilis, HTLV y Core; </b> a continuacin, se explican estas.

    AcordionA.mb-5(clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="VIH").pb-3.px-3
        .col.mb-4.mb-md-0
          p.mt-3 El VIH es el virus de Inmunodeficiencia Humana, que se encarga de daar el sistema inmunitario, es decir, a los glbulos blancos que son los encargados de defender al cuerpo de las enfermedades. Por lo tanto, dicho virus expone a la persona a que fcilmente pueda enfermarse, ya que destruye sus defensas, y no tiene quien la proteja. El VIH se puede transmitir por va sexual (anal, vaginal), por transfusin o por va vertical (madre a hijo).

      .row(titulo="Hepatitis B").pb-3.px-3
        .col.mb-4.mb-md-0
          p.mt-3 La hepatitis B es una enfermedad causada por el virus de la hepatitis B que produce una infeccin heptica grave. Para algunas personas, la infeccin de la hepatitis B se vuelve crnica, lo que significa que dura ms de seis meses. <b>Tener hepatitis B crnica aumenta el riesgo de contraer insuficiencia heptica, cncer de hgado o cirrosis, enfermedad esta que causa cicatrices permanentes en el hgado.</b> Se puede transmitir por contacto sexual sin proteccin, por contacto con agujas o por va vertical (madre-hijo). El anti-core es una sustancia denominada anticuerpo y se encuentra en la hepatitis B aguda y crnica, es decir que cuando dicha sustancia est presente, hace evidente la presencia de aquellas hepatitis B ocultas.

      .row(titulo="Hepatitis C").pb-3.px-3
        .col.mb-4.mb-md-0
          
          p.mt-3 Es una infeccin del hgado causada por el virus de la hepatitis C y puede llevar a la persona a una insuficiencia heptica, cncer de hgado o enfermedad heptica crnica (cirrosis). Las infecciones generadas por este virus son de las principales causas que conllevan a las personas a ser trasplantadas. Algunas personas que contraen el VHC solo padecen una enfermedad de corta duracin porque sus cuerpos son capaces de eliminar el virus. Pero la mayora de la gente infectada desarrolla una infeccin crnica (de larga duracin) por el VHC. Se transmite por compartir agujas, por transfusin de sangre.

      .row(titulo="Enfermedad de Chagas").pb-3.px-3
        .col.mb-4.mb-md-0          
          p.mt-3 La enfermedad de Chagas es una enfermedad inflamatoria e infecciosa causada por el parsito conocido como el #[i Trypanosoma cruzi], el cual se encuentra en el sistema digestivo de unos insectos llamados triatominos, pero que comnmente se le conoce como el pito. Estos insectos suelen habitar en construcciones que estn hechas de barro, y lo que hacen es chupar la sangre de las personas y posteriormente defecan. La herida causada produce rasquia, entonces al rascarse la persona lleva las heces del insecto al torrente sanguneo. Estas heces contienen el parsito #[i Trypanosoma cruzi], el cual afecta el corazn e incluso el estmago del husped. La enfermedad de Chagas tambin puede propagarse a travs de alimentos contaminados, una transfusin de sangre, un rgano donado o de la madre al beb durante el embarazo.

      .row(titulo="Sfilis").pb-3.px-3
        .col.mb-4.mb-md-0          
          p.mt-3 Es una enfermedad de transmisin sexual y puede causar implicaciones a largo plazo, e incluso hasta la muerte, si no se realiza el tratamiento respectivo. Se puede transmitir mediante contacto directo en relaciones sexuales vaginales, anales u orales, tambin se puede transmitir de la mam al hijo en gestacin.

      .row(titulo="HTLV").pb-3.px-3
        .col.mb-4.mb-md-0          
          p.mt-3 Es un virus conocido como linfotrpico de clulas T humanas, infecta los glbulos blancos y puede causar enfermedades del sistema nervioso o leucemia. El virus se transmite por contacto sexual, consumo de drogas inyectadas de forma ilegal, contacto con sangre o a travs de la lactancia.  

    .cajon.cajon--gris.mb-4
      .row.justify-content-center.align-items-center
        .col-auto
          figure
            img(src="@/assets/curso/images/pages/ilustraciones/ico_24.svg", alt="alt", style="width:120px;")         
        .col-sm
          p Es importante aclarar que las pruebas serolgicas realizadas en banco de sangre son pruebas presuntivas, es decir que son de sospecha, ya que se supone que quien se acerca a donar es una persona que considera tener hbitos de vida saludables, y las pruebas se realizan con el fin de llegar a reducir cualquier riesgo de infeccin a travs de la transfusin.

    p.mb-5 Si algn marcador infeccioso resulta doblemente reactivo, deber realizarse una prueba confirmatoria, es decir, una prueba para realmente saber si dicha persona est positiva a la infeccin. En caso de estarlo, <b> el banco de sangre debe ubicar a dicha persona que pasa de ser donante a paciente, puesto que con el resultado de la prueba se evidencia que est enfermo,</b> lo ubica para notificarle el resultado, asesorar y canalizarlo a su aseguradora para que se le brinde el tratamiento y seguimiento respectivo. 

    .h5.mb-4 Formas de donar sangre    

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/doLmh1vANX0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Formas de donar sangre   
        
    .h5 Tipos de donantes de sangre

    p La donacin de sangre es un acto de solidaridad, y en Colombia la normativa vigente reitera que es voluntario y no debe darse remuneracin a cambio de esta, como lo establece el Decreto 1571 de 1993: La salud es un bien de inters pblico, donar sangre es un deber de solidaridad social que tienen las personas y, por ningn motivo, podr ser remunerado (Art. 28).  
    br
    p Se dice que el primer eslabn de la cadena transfusional es la promocin, que debe propender por donantes de sangre voluntarios, de bajo riesgo, que donen de forma libre.
    br
    p.mb-4 De la misma forma, el Decreto define:

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-6.mb-5.mb-lg-0
        .bloque-texto-b.p-4
          .bloque-texto-b__texto.h4.mb-0 Donante de sangre: persona que, previo el cumplimiento de los requisitos sealados en este Decreto, da, sin retribucin econmica y a ttulo gratuito y para fines preventivos, teraputicos de diagnstico o de investigacin, una porcin de su sangre en forma voluntaria, libre y consciente.
          br
          .h6.z-index Art. 3     

    p.mb-5 En general, hay diferentes situaciones que promueven que una persona realice una donacin de sangre, <b> los tipos de donantes en Colombia  se definen de la siguiente forma:</b>

    hr
    .row.align-items-center
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/A1.svg", alt="alt", style="width:90px;")
      .col
        h6 Autlogo
        p "Es aquella persona a la cual se le extrae sangre total o componentes sanguneos reservados exclusivamente para ella" (Instituto Nacional de Salud and Ministerio de Salud y Proteccin Social 2018; Ministerio de Salud 1993).
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/A2.svg", alt="alt", style="width:90px;")
      .col
        h6 Voluntario
        p "Es una persona que dona su sangre, plasma u otros componentes por su propia voluntad, libremente y sin recibir dinero, ni otro tipo de pago que puede considerarse sustituto del dinero. Pequeos suvenires, refrigerios y reembolsos de los gastos directos asociados al viaje /transporte son compatibles con este tipo de donacin" (Instituto Nacional de Salud and Ministerio de Salud y Proteccin Social 2018; Organizacin Mundial de la Salud 2001; Organizacin Panamericana de la Salud 2005).
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/A3.svg", alt="alt", style="width:90px;")
      .col
        h6 Voluntario habitual, repetitivo / de repeticin
        p "Persona que dona sangre o componentes por lo menos 2 veces en los ltimos 12 meses, por su propia voluntad y no recibe ningn pago, ya sea en forma de dinero en efectivo o en especie que podra considerarse un sustituto del dinero" (Instituto Nacional de Salud and Ministerio de Salud y Proteccin Social 2018).
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/A4.svg", alt="alt", style="width:90px;")
      .col
        h6 Dador por reposicin
        p "Es la persona que dona sangre o componentes, previo proceso de sensibilizacin y educacin, cuando es requerida por un miembro de su familia o comunidad sin que haya exigencia de por medio para la prestacin del servicio" (Instituto Nacional de Salud and Ministerio de Salud y Proteccin Social 2018; Organizacin Mundial de la Salud 2001).
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/A5.svg", alt="alt", style="width:90px;")
      .col
        h6 Dirigido
        p "Persona que dona sangre para un receptor determinado; ejemplo: madre que dona plaquetas para su hijo, donante de rin que adems dona su sangre para el receptor del rgano. La sangre debe llegar a la persona para la cual se don y si este no es el caso se deben considerar como de reposicin" (Instituto Nacional de Salud and Ministerio de Salud y Proteccin Social 2018).
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/A6.svg", alt="alt", style="width:90px;")
      .col
        h6 Voluntario convocado
        p "Donante voluntario, que por sus caractersticas fenotpicas e inmunohematolgicas es llamado por un banco de sangre a donar para un paciente que dadas sus caractersticas fenotpicas requiere esta donacin por ser la ms compatible para l" (Instituto Nacional de Salud and Ministerio de Salud y Proteccin Social 2018)

    hr.mb-5   

    p.mb-5 De acuerdo con lo anterior y segn la seguridad transfusional, <b> las acciones realizadas por el promotor de la donacin, deben buscar la captacin de los donantes habituales de sangre,</b> razn por la cual, se requiere que las estrategias educativas permitan que cuando donen aquellas personas que lo hacen de manera voluntaria y por primera vez, tomen la decisin de hacerlo si consideran no tener factores de riesgo de infeccin y que a partir de ese momento conciben la donacin como una accin social que se debe realizar frecuentemente, logrando de esta manera incrementar el porcentaje de donante habitual.

    #t_1_2.h4.mt-4 1.2 Seguimiento de indicadores
    .titulo-segundo

    p.mb-5 La gestin a realizar por parte del grupo de promocin, siempre debe estar basada en un anlisis previo de los indicadores, si bien es cierto, en Colombia los bancos de sangre notifican datos estadsticos a partir de los cuales se generan los diferentes indicadores, sin embargo, se han definido como los prioritarios a seguir, por los bancos de sangre: 

    .row.align-items-start.mb-5
      .col-12.col-lg-3.mb-4.hg--full
        .crd-border.crd-border--blue
          figure.mb-4
            img(src="@/assets/curso/images/pages/ilustraciones/ico_28.svg", alt="alt", style="width:50px; display:block; margin:0 auto;")
          p.text-center #[strong PORCENTAJE DE DONACIN HABITUAL]

      .col-12.col-lg-3.mb-4.hg--full
        .crd-border.crd-border--darkBlue
