            img(src="@/assets/curso/images/pages/ilustraciones/ico_29.svg", alt="alt", style="width:50px; display:block; margin:0 auto;")
          p.text-center #[strong PORCENTAJE DE REACTIVIDAD ACUMULADA]

      .col-12.col-lg-3.mb-4.hg--full
        .crd-border.crd-border--blue
          figure.mb-4
            img(src="@/assets/curso/images/pages/ilustraciones/ico_30.svg", alt="alt", style="width:50px; display:block; margin:0 auto;")
          p.text-center #[strong PORCENTAJE DE INCINERACIN POR VENCIMIENTO]

      .col-12.col-lg-3.mb-4.hg--full
