            img(src="@/assets/curso/images/pages/ilustraciones/ico_31.svg", alt="alt", style="width:50px; display:block; margin:0 auto;")
          p.text-center #[strong TASA DE POSITIVIDAD]   


    p.mb-5 Para su anlisis, su calificacin debe estar basada en los siguientes niveles, dependiendo de su interpretacin:
  
    .h6.mb-5 Porcentaje de donacin habitual:

    .row.justify-content-center.align-items-start.mb-5    
      .col-12.col-lg.hg--full.mb-4
        .crd-border.crd-border--blue
          figure.mb-4
            img(src='@/assets/curso/images/pages/ilustraciones/ico_44.svg' alt="", style="width:35px;")            
          .h6 #[strong Satisfactorio]
          p Dicha calificacin se da cuando el dato obtenido es superior al promedio nacional  departamental en relacin con su histrico.

      .col-12.col-lg.hg--full.mb-4
        .crd-border.crd-border--darkBlue        
          figure.mb-4
            img(src='@/assets/curso/images/pages/ilustraciones/ico_45.svg' alt="", style="width:35px;")            
          .h6 #[strong Aceptable]
          p Dicha calificacin se da cuando el dato obtenido est ubicado en el promedio nacional - departamental en relacin con su histrico.         
      
      .col-12.col-lg.hg--full.mb-4
        .crd-border.crd-border--darkBlue           
          figure.mb-4
            img(src='@/assets/curso/images/pages/ilustraciones/ico_46.svg' alt="", style="width:35px;")          
          .h6 #[strong Insatisfactorio]
          p Dicha calificacin se da cuando el dato obtenido est ubicado por debajo del promedio nacional - departamental en relacin con su histrico.
      

    .h6.mb-5 Porcentaje de reactividad acumulada, porcentaje de incineracin por vencimiento, tasa de positividad:

    .row.justify-content-center.align-items-start.mb-5     
      .col-12.col-lg.hg--full.mb-4.mb-lg-0
        .crd-border.crd-border--blue
          figure.mb-4
            img(src='@/assets/curso/images/pages/ilustraciones/ico_44.svg' alt="", style="width:35px;")            
          .h6 #[strong Satisfactorio]
          p Dicha calificacin se da cuando el dato obtenido es inferior al promedio nacional  departamental en relacin con su histrico.

      .col-12.col-lg.hg--full.mb-4.mb-lg-0
        .crd-border.crd-border--darkBlue        
          figure.mb-4
            img(src='@/assets/curso/images/pages/ilustraciones/ico_45.svg' alt="", style="width:35px;")            
          .h6 #[strong Aceptable]
          p Dicha calificacin se da cuando el dato obtenido est ubicado en el promedio nacional - departamental en relacin con su histrico.      
      
      .col-12.col-lg.hg--full.mb-4.mb-lg-0
        .crd-border.crd-border--darkBlue           
          figure.mb-4
            img(src='@/assets/curso/images/pages/ilustraciones/ico_46.svg' alt="", style="width:35px;")          
          .h6 #[strong Insatisfactorio]
          p Dicha calificacin se da cuando el dato obtenido es superior al promedio nacional - departamental en relacin con su histrico.  

    p.mb-5 As mismo, para el anlisis de los indicadores se debe tener en cuenta:

    hr
    h6 Periodicidad:
    p Revisin de cortes trimestrales, semestrales o anuales.
    hr
    h6 Notificacin:
    p Las coordinaciones departamentales deben hacer seguimiento mensual al nmero de actores que reportan en SIHEVI-INS, tanto bancos, como servicios de transfusin, acompaamiento a aquellas instituciones que no reporten, en pro de facilitar el reporte, y notificar la apertura o cierre de servicios de transfusin, para comunicarlo al nivel nacional.
    hr
    h6 Anlisis.
    hr
    h6 Reportes de servicios de transfusin.
    hr
    h6 Reportes de gestin de bancos.
    hr
    h6 Reporte de pruebas confirmatorias.
    hr
    h6 Mecanismos de exportacin.
    hr
    h6 Acciones para realizar:
    p En este ltimo aparte, cada secretaria departamental o distrito capital debe hacer seguimiento peridico al comportamiento de indicadores de actores de su rea de influencia, tanto para bancos, como para servicios (Instituto Nacional de Salud, 2017).
    hr.mb-5

    .crd_A.crd_A--azul.mb-5
      .row.justify-content-center.align-items-center
        .col-auto.mb-4.mb-lg-0
          figure
            img(src="@/assets/curso/images/pages/ilustraciones/ico_32.svg", alt="alt", style="width: 250px; display:block; margin:0 auto;")
        .col-12.col-lg
          p Si un indicador presenta un comportamiento insatisfactorio, <b> el grupo de promocin del banco de sangre deber disear un plan de mejora con actividades que contribuyan con el fortalecimiento del comportamiento del indicador, </b> al cual el rea de calidad del banco de sangre e incluso la coordinacin departamental, segn sea el caso, realizar el seguimiento respectivo para verificar su cumplimiento. 

    p.mb-4 El porcentaje de tipo de donante de sangre debe ser analizado de la mano con las tasas de positividad, esto quiere decir que debe existir una coherencia entre los mismos. Por ejemplo, si el porcentaje de donante habitual est aumentando, siendo este considerado como un tipo de donante que se ha caracterizado por ser uno de los de menor riesgo de transmisin de infecciones, al observar las tasas de positividad estas deben estar bajas; en caso de que estn altas, se tiene que revisar si realmente es un donante habitual.<b> Es de resaltar que este donante no solo est ligado a la frecuencia de donacin (2 veces en 12 meses), sino que, adems, debe tener un comportamiento infeccioso bajo.</b> 
    p.mb-5 Tener presente que si los donantes voluntarios por primera vez estn presentando una tasas altas de positividad en los diferentes marcadores infecciosos, esto quiere decir que el mensaje brindado en un primer momento debe ser revisado, ya que seguramente es un mensaje superficial y no se le est informando en aspectos relevantes, como por ejemplo, periodo de ventana inmunolgica.

    .row.justify-content-center.align-items-start.mb-5
      .col-12.col-lg-4.mb-4.mb-lg-0
        .h5 <b>Si un donante voluntario por primera vez no retorna, el promotor debe ser consciente de la importancia de revisar y tomar acciones de mejora.</b>
      .col-12.col-lg-8
        p As mismo, es importante observar si al captar donantes voluntarios por primera vez, estos retornan en menos de 12 meses, es decir, si son fidelizados y se convierten en donantes habituales. Adems, si un donante voluntario por primera vez no retorna, el promotor debe ser consciente de la importancia de revisar y tomar acciones de mejora con respecto a las <b>estrategias de informacin, sensibilizacin y educacin que estn implementando.</b>

    p.mb-4 Si los donantes voluntarios por primera vez estn presentando tasas altas de positividad en los diferentes marcadores infecciosos, esto quiere decir que el mensaje brindado en el primer momento que fue abordado requiere ser revisado, ya que seguramente, es un mensaje superficial y no se le est informando en aspectos relevantes, como, por ejemplo, periodo de ventana inmunolgica.

    .row.justify-content-center.align-items-center.mb-5
      .col-auto
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_10.png", alt="alt", style="width:1400px;")      
    
    p.mb-4 Por otro lado, es fundamental que desde el rea de promocin se analice el indicador de porcentaje de incineracin de glbulos rojos por vencimiento (est centrado en glbulos rojos, debido a que es el hemocomponente que ms se transfunde), ya que este indicador pone de manifiesto si previamente al realizar una jornada de donacin se est analizando el comportamiento de los inventarios de hemocomponentes, los cuales indican el nmero de campaas que debe realizar dicho banco de sangre (semanal - mensual), la meta de captacin por cada campaa y qu grupos sanguneos son los que deben colectar.    

    .cajon.cajon--gris.p-4.mb-4   
      p.mb-0 Si se estn incinerando hemocomponentes por vencimiento, esto quiere decir que <b> seguramente se est captando ms de lo que se necesita </b> o que se estn captando grupos sanguneos que no son requeridos en el momento.

    p.mb-5 Un promotor de la donacin debe soportar sus decisiones en los indicadores, por eso se insiste en la importancia de realizar el anlisis de su comportamiento. 

    .titulo-segundo
      #t_1_3.h4 1.3 Reacciones adversas a la donacin y a la transfusin

    p.mb-5 A nivel de medicina transfusional se dice que la mejor transfusin es aquella que no se hace. La transfusin de hemocomponentes da a da salva miles de vidas en el mundo; sin embargo, dado que los hemocomponentes provienen de seres humanos, la administracin de estos, <b> siempre tienen un riesgo para los pacientes que los reciben,</b> (este riesgo puede estar asociado a los donantes o a los mismos hemocomponentes). En este marco se han creado sistemas de hemovigilancia, entendida esta como:

    .crd_A.crd_A--azul.mb-5   
      p <b>"Un conjunto de procedimientos de vigilancia que cubren toda la cadena transfusional, desde la donacin y procesamiento de sangre y sus componentes, hasta su provisin y transfusin a los pacientes y su seguimiento. Incluye el monitoreo, reporte, investigacin y anlisis de eventos adversos relacionados con la donacin, procesamiento y transfusin de sangre, y tomar acciones para prevenir su ocurrencia o recurrencia".</b>
      br
      p World Health Organization WHO, 2016.

    p As, la hemovigilancia comprende el seguimiento vena a vena, <b>desde que el hemocomponente sale del brazo del donante, hasta que se administra al receptor.</b>
    br
    p A nivel internacional, se ha recomendado la implementacin de sistemas nacionales de hemovigilancia en los pases y en Colombia se implement desde el ao 2002 con el desarrollo del proyecto <b>Epi-blood de OPS/OMS</b> y en 2010 se public el <b>Manual de hemovigilancia del Instituto Nacional de Salud,</b> como Coordinador de la Red. En dicho ao se gener un manual que daba las pautas para el desarrollo del sistema de hemovigilancia, que tiene como principios: la voluntariedad, universalidad, confidencialidad y que no sea punible. (Instituto Nacional de Salud-Coordinacin Red Nacional de Bancos de Sangre y Servicios de Transfusin and Instituto Nacional de Salud 2010).
    br
    p.mb-5 El sistema de hemovigilancia se implementa para <b>identificar las reacciones adversas y mejorar la seguridad del sistema transfusional,</b> as mismo, con la deteccin tomar acciones para prevenir que dichas situaciones se vuelvan a presentar.

    .tarjeta--container.row.mb-5
      .col-md.tarjeta.tarjeta--roja-1.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/images/pages/ilustraciones/ico_33.svg', alt='Texto que describa la imagen')
          
        .h3.text-center Reacciones Adversas a la Donacin (RAD)
        p En este marco, el sistema permite la notificacin de Reacciones Adversas a la Donacin (RAD), que puedan presentarse en los donantes.

      .col-md.tarjeta.tarjeta--roja-2.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/images/pages/ilustraciones/ico_34.svg' alt="", style="width:250px; display:block; margin:auto 0;").img-float
        .h3.text-center Reacciones Adversas a la Transfusin (RAT)
        p Pueden presentarse en los pacientes que reciben la transfusin. Estas pueden ser de tipo agudo no infeccioso, reacciones transfusionales tardas no infecciosas e Infecciones Transmitidas por la Transfusin (ITT).

    p Actualmente <b>las RAT son notificadas por los actores en el sistema de hemovigilancia SIHEVI-INS.</b>
    br
    p.mb-5 Siguiendo al Instituto Nacional de Salud (2020), las RAT tambin se clasifican de acuerdo con el grado de severidad:

    .row.mb-5
      .col-sm-6.col-lg-3.mb-4.mb-lg-0
        .tarjeta.tarjeta--boton.p-4
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/images/pages/ilustraciones/ico_35.svg' alt="", style="width:100px; display:block; margin:0 auto;")

          .h5.text-center GRADO 1
          p.text-small.text-center No severo
  
      .col-sm-6.col-lg-3.mb-4.mb-lg-0
        .tarjeta.tarjeta--boton.p-4
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/images/pages/ilustraciones/ico_36.svg' alt="", style="width:100px; display:block; margin:0 auto;").img-float

          .h5.text-center GRADO 2
          p.text-small.text-center Severo
  
      .col-sm-6.col-lg-3.mb-4.mb-sm-0
        .tarjeta.tarjeta--boton.p-4
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/images/pages/ilustraciones/ico_37.svg' alt="", style="width:80px; display:block; margin:0 auto;").img-float

          .h5.text-center GRADO 3
          p.text-small.text-center Amenaza a la vida
  
      .col-sm-6.col-lg-3
        .tarjeta.tarjeta--boton.p-4
          .row.justify-content-center.mb-3
            .col-7
              figure
                img(src='@/assets/curso/images/pages/ilustraciones/ico_38.svg' alt="", style="width:85px; display:block; margin:0 auto;").img-float

          .h5.text-center GRADO 4
          p.text-small.text-center Muerte

    p.mb-4 Uno de los grandes retos de los promotores de la donacin de sangre, es atribuirles el menor riesgo transfusional a los pacientes, lo cual es posible implementando las acciones tcnicas necesarias con el fin de captar donantes de bajo riesgo de transmitir algn tipo de infeccin. Razn por la cual el promotor debe informar adecuadamente a los donantes potenciales, para que quienes tomen la decisin de donar, lo hagan basados en que <b> sus conductas de vida no conllevaran a transmitir infecciones como el VIH, Hepatitis B, sfilis, entre otros.</b>

    p De acuerdo con lo anterior, la gestin de los promotores impacta positiva o negativamente las tasas de positividad en los diferentes marcadores infecciosos, de ah la importancia de realizar un seguimiento continuo a este indicador, y as, tomar las acciones que se requieran para ajustar los procesos relacionados con el rea de promocin de la donacin.
    br    
    p Es trascendental recalcar, entonces, <b> la importancia de que el promotor conozca el concepto de ventana inmunolgica, ya que es el tiempo en el cual una persona est enferma pero los niveles de dicha enfermedad estn tan bajos que no pueden ser an detectables,</b> lo cual quiere decir que si esa persona llega a donar en ese momento podra infectar con su sangre a aquellos pacientes a los cuales les fuese transfundida la sangre. 
    br
    p.mb-5 Los dos escenarios anteriores son primordiales, ya que si se observan altas tasas de positividad, esto quiere decir que el promotor est captando donantes de alto riesgo, lo cual va en contra de la seguridad transfusional y, adems, est permitiendo que no haya eficiencia en el proceso, es decir que los recursos invertidos no estn siendo aprovechables. Por otro lado, <b> si se llega a pasar una unidad de sangre en el periodo de ventana inmunolgica, esto podra generar consecuencias ticas, legales, penales y econmicas. </b>

    .row.justify-content-center.align-items-center.mb-5
      figure
        img(src="@/assets/curso/images/pages/ilustraciones/img_11.png", alt="alt", style="width:1200px;") 

    p Por otro lado, es relevante que se informe a los donantes potenciales sobre los cuidados que debe tener antes, durante y despus de la donacin de sangre, ya que este tipo de informacin contribuye a que el donante no presente reacciones adversas a la donacin.
    br
    p Dejar que los donantes de sangre tengan una reaccin adversa a la donacin, es una de las mayores causas por las cuales dichas personas no retornan a donar y, adems, comparten su experiencia negativa con otros, impidindoles que ejerzan este acto de amor y solidaridad.   
    br
    p.mb-4 De otra parte, es fundamental que el promotor siempre indague en los donantes potenciales de sangre, el tiempo de ayuno que tiene, ya que este no puede superar las cuatro horas; adems debe preguntarle, cules fueron los ltimos alimentos que consumi, ya que muchas veces, sobre todo en las maanas, simplemente les indagan si desayunaron, pero no preguntan a qu hora, ni qu desayun.

    .cajon.cajon--rojo.p-4.mb-4
      .row.justify-content-center.align-items-center
        .col-auto
          img.mt-3(src="@/assets/curso/images/pages/ilustraciones/ico_39.svg" alt="", style="width:120px;")
        .col
          p Es importante hacerle ver a los donantes potenciales que <b> el refrigerio es una medida de seguridad que tienen el banco de sangre para disminuir las posibilidades de tener una reaccin adversa a la donacin.</b> As el donante podr percibir que su bienestar es una prioridad para dicha institucin. 
    
    p Anualmente, y gracias al sistema de informacin que se maneja en la Red Nacional de Bancos de Sangre y Servicios de Transfusin, se recogen diversas variables que permiten realizar anlisis, respecto a algunos de los principales indicadores de inters para la cadena de procesos transfusionales.
    br
    p.mb-4 Desde la Coordinacin de la Red Nacional de bancos de sangre del Instituto Nacional de Salud, se elaboran informes de gestin, de forma anual, detallados por departamento y variable colectada, que son publicados en la <b> pgina web del INS www.ins.gov.co.</b> All se recoge la informacin estadstica de bancos de sangre y servicios de transfusin y se hace seguimiento a indicadores, a travs de:

    .row.mb-5
      .col-md-6.col-lg.mb-4.mb-lg-0
        .tarjeta-Avatar
          img(src='@/assets/curso/images/pages/ilustraciones/ico_40.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris.p-4
            .h4.text-center Informes nacionales de gestin Red de Bancos de Sangre y Servicios de Transfusin

      .col-md-6.col-lg.mb-4.mb-lg-0
        .tarjeta-Avatar
          img(src='@/assets/curso/images/pages/ilustraciones/ico_41.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris.p-4
            .h4.text-center Informes nacionales de pruebas confirmatorias  

      .col-md-6.col-lg.mb-4.mb-lg-0
        .tarjeta-Avatar
          img(src='@/assets/curso/images/pages/ilustraciones/ico_42.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris.p-4
            .h4.text-center Informes de hemovigilancia
            
    p Dichos informes permiten conocer el comportamiento del pas y por departamento; y as mismo permiten que cada actor pueda comparar su propio comportamiento, es por esto que <b>el promotor debe estar atento a revisar este tipo de informes y analizarlos.</b>
  
